.App {
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.topBar{
  margin-top: 2%;
}

.App-header {
  background-color: #282c34;
  display: flex;
  align-items: center;
  color: white;
  width: 100vw;
  height: 10vh;
  position: fixed;
  top: 0;
  z-index: 3;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.buttonCustom {
  cursor: pointer;
  padding-left: 2%;
  padding-right: 2%;
  background-color: white;
  color: #282c34;
  border: #282c34 2px solid;
  margin: 1%;
  box-shadow: inset 0 0 0 0 #282c34;
  transition: ease-out 0.4s;
  text-align: center;
}
.buttonCustom:hover{
  color: white;
  background-color: #282c34;
}
.bigText{
  font-size: medium;
}

.selectedButton {
  margin: 1%;
  padding-left: 2%;
  padding-right: 2%;
  color: white;
  background-color: #282c34;
  border: #282c34 2px solid;
}

.home {
  margin-top: 10vh;
}
.m-1{
  margin: 1%;
}
.ml-1{
  margin-left: 5%;
  margin-right: 5%;
}
.catalog{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.cardCustom{
  padding: 2%;
  border: 1px #ddd solid;
}